.theme-light {
  --html-scrollbar-thumb-color: #c1c1c1;
  --html-scrollbar-track-color: #f1f1f1;
  --main-background-color: #006F74;
  --default-game-name-color: #000000;
  --default-game-slider-color: #ffffff;
  --default-game-text-color: #000000;
  --default-game-text-background-color: #d9d9d9;
  --default-game-text-scrollbar-thumb-color: #808080;
  --default-game-text-scrollbar-track-color: #ffffff;
  --pacman-background-color: #36B688;
  --pacman-text-background-color: #d9d9d9;
  --flappybird-background-color: #81CDC2;
  --flappybird-name-color: #000000;
  --flappybird-text-background-color: #d9d9d9;
  --spaceInvaders-background-color: #FEF2D7;
  --spaceInvaders-name-color: #000000;
  --spaceInvaders-text-background-color: #d9d9d9;
  --spaceInvaders-slider-color: #DC8383;
  --tetris-background-color: #F38C76;
  --tetris-name-color: #000000;
  --tetris-text-background-color: #d9d9d9;
  --tetris-slider-color: #DC8383;
  --mario-background-color: #F24E4A;
  --mario-name-color: #000000;
  --mario-text-background-color: #d9d9d9;
  --mario-slider-color: #470A0A;
  --pong-background-color: #D72626;
  --pong-name-color: #000000;
  --pong-text-background-color: #d9d9d9;
  --pong-slider-color: #470A0A;
  --footer-background-color: #D72626;
  --footer-span-color: #470A0A;
}

.theme-dark {
  --html-scrollbar-thumb-color: #969696;
  --html-scrollbar-track-color: #2c2c2c;
  --main-background-color: #01002F;
  --default-game-name-color: #000000;
  --default-game-slider-color: #ffffff;
  --default-game-text-color: #ffffff;
  --default-game-text-background-color: #d9d9d9;
  --default-game-text-scrollbar-thumb-color: #969696;
  --default-game-text-scrollbar-track-color: #2c2c2c;
  --pacman-background-color: #023E29;
  --pacman-text-background-color: #01110B;
  --flappybird-background-color: #014339;
  --flappybird-name-color: #131918;
  --flappybird-text-background-color: #041E1A;
  --spaceInvaders-background-color: #573D02;
  --spaceInvaders-name-color: #5B0909;
  --spaceInvaders-text-background-color: #5B0909;
  --spaceInvaders-slider-color: #DC8383;
  --tetris-background-color: #320A01;
  --tetris-name-color: #B82626;
  --tetris-text-background-color: #992121;
  --tetris-slider-color: #992121;
  --mario-background-color: #6A0B08;
  --mario-name-color: #EE3630;
  --mario-text-background-color: #340B0A;
  --mario-slider-color: #470A0A;
  --pong-background-color: #3D0505;
  --pong-name-color: #685858;
  --pong-text-background-color: #150202;
  --pong-slider-color: #150202;
  --footer-background-color: #3D0505;
  --footer-span-color: #685858;
}

.animation-theme-transition {
  transition: color 0.5s, background 0.5s;
}

html {
  scrollbar-color: var(--html-scrollbar-thumb-color) var(--html-scrollbar-track-color);
}

.x-sidebar-container {
  background-color: var(--main-background-color);
}

.x-main-container {
  background-color: var(--main-background-color);
}

.x-pacMan-container {
  background-color: var(--pacman-background-color);
}
.x-pacMan-container .x-game-text-container {
  background-color: var(--pacman-text-background-color);
}

.x-flappyBird-container {
  background-color: var(--flappybird-background-color);
}
.x-flappyBird-container .x-game-name-container {
  color: var(--flappybird-name-color);
}
.x-flappyBird-container .x-game-text-container {
  background-color: var(--flappybird-text-background-color);
}

.x-spaceInvaders-container {
  background-color: var(--spaceInvaders-background-color);
}
.x-spaceInvaders-container .x-game-name-container {
  color: var(--spaceInvaders-name-color);
}
.x-spaceInvaders-container .x-game-text-container {
  background-color: var(--spaceInvaders-text-background-color);
}
.x-spaceInvaders-container .x-slider-button {
  color: var(--spaceInvaders-slider-color);
}

.x-tetris-container {
  background-color: var(--tetris-background-color);
}
.x-tetris-container .x-game-name-container {
  color: var(--tetris-name-color);
}
.x-tetris-container .x-game-text-container {
  background-color: var(--tetris-text-background-color);
}
.x-tetris-container .x-slider-button {
  color: var(--tetris-slider-color);
}

.x-mario-container {
  background-color: var(--mario-background-color);
}
.x-mario-container .x-game-name-container {
  color: var(--mario-name-color);
}
.x-mario-container .x-game-text-container {
  background-color: var(--mario-text-background-color);
}
.x-mario-container .x-slider-button {
  color: var(--mario-slider-color);
}

.x-pong-container {
  background-color: var(--pong-background-color);
}
.x-pong-container .x-game-name-container {
  color: var(--pong-name-color);
}
.x-pong-container .x-game-text-container {
  background-color: var(--pong-text-background-color);
}
.x-pong-container .x-slider-button {
  color: var(--pong-slider-color);
}

.x-game-text-container {
  color: var(--default-game-text-color);
  background-color: var(--default-game-text-background-color);
  scrollbar-color: var(--default-game-text-scrollbar-thumb-color) var(--default-game-text-scrollbar-track-color);
}

.x-game-name-container {
  color: var(--default-game-name-color);
}

.x-slider-button {
  color: var(--default-game-slider-color);
}

footer {
  background-color: var(--footer-background-color);
}
footer span:hover {
  color: var(--footer-span-color);
}

@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
  html {
    padding: 0;
    margin: 0;
    font-family: "PressStart2P";
    overflow-x: hidden;
  }
  body {
    padding: 0;
    margin: 0;
    overflow-x: hidden;
  }
  .active {
    display: block !important;
  }
}
@font-face {
  font-family: "PressStart2P";
  src: url("../assets/fonts/PressStart2P-Regular.ttf");
}